@keyframes shapeAFadeInLeft {
    from {
        opacity: 0;
        top: -25%;
        left: -15%;
        transform: rotate(50deg);
    }
    to {
        opacity: 1;
        top: -100px;
        left: -100px;
        transform: rotate(20deg);
    }
}

.shapeAAnim {
    animation-name: shapeAFadeInLeft;
}

@media screen and (max-width: 959px) {
    @keyframes avatarInBottom {
        from {
            box-shadow: 0 0;
            opacity: 0;
            bottom: -280px;
            transform: rotate(50deg);
        }
        50% {
            bottom: 40px;
        }
        to {
            opacity: 1;
            bottom: 0;
            transform: rotate(0deg);
            boxShadow: '0 10px 10px -8px rgba(0,0,0,.78)',
        }
    }
}

@media screen and (min-width: 959px) {
    @keyframes avatarInBottom {
        from {
            box-shadow: 0 0;
            opacity: 0;
            bottom: -460px;
            transform: rotate(50deg);
        }
        50% {
            bottom: 25px;
        }
        to {
            opacity: 1;
            bottom: 0;
            transform: rotate(0deg);
            boxShadow: '0 10px 10px -8px rgba(0,0,0,.78)',
        }
    }
}

.avatarAnim {
    animation-name: avatarInBottom;
}

@media screen and (max-width: 959px) {
    @keyframes headerInfoAvatarPadding {
        50% {
            padding: 30px;
        }
        to {
            padding: 10px 20px;
        }
    }
}

.headerInfoAvatarAnim {
    animation-name: headerInfoAvatarPadding;
}
